  }
}

.select2-selection--single {
  background-color: transparent;
  border: 0;
  .select2-selection__rendered {

    line-height: 28px;
  }

  .select2-selection__clear {
    cursor: pointer;
    float: right;
    font-weight: bold;
  }

  .select2-selection__placeholder {
    color: #999;
  }

  .select2-selection__arrow {
    height: 26px;

    position: absolute;

    top: 1px;
    right: 1px;

    width: 20px;

    b {
      border-color: #888 transparent transparent transparent;
      border-style: solid;
      border-width: 5px 4px 0 4px;

      height: 0;
      left: 50%;

      margin-left: -4px;
      margin-top: -2px;

      position: absolute;

      top: 50%;
      width: 0;
    }
  }
}

&[dir="rtl"] {
  .select2-selection--single {
    .select2-selection__clear {
      float: left;
    }

    .select2-selection__arrow {
      left: 1px;
      right: auto;
    }
  }
}

&.select2-container--disabled {
  .select2-selection--single {
    background-color: #eee;
    cursor: default;

    .select2-selection__clear {
      display: none;
    }
  }
}

&.select2-container--open {
  .select2-selection--single {
    .select2-selection__arrow {
      b {
        border-width: 0 4px 5px 4px;
      }
    }
  }
}

.select2-selection--multiple {

  border-radius: 4px;
  cursor: text;

  .select2-selection__rendered {
    box-sizing: border-box;
    list-style: none;
    margin: 0;
    padding: 0 5px 0 0;
    width: 100%;

    li {
      list-style: none;
    }
  }

  .select2-selection__placeholder {
    color: #999;

    margin-top: 5px;

    float: left;
  }

  .select2-selection__clear {
    cursor: pointer;
    float: right;
    font-weight: bold;
    margin-top: 5px;
    margin-right: 10px;
  }

  .select2-selection__choice {
    // background-color: #e4e4e4;


    border-radius: 4px;
    cursor: default;

    float: left;

    margin-right: 5px;
    margin-top: 5px;
    padding: 0 5px 0 0;
  }

  .select2-selection__choice__remove {
    color: #999;
    cursor: pointer;
    display: none;
    // display: inline-block;
    font-weight: bold;

    margin-right: 2px;

    &:hover {
      // color: #333;
    }
  }
}

.select2-container--open .select2-selection__choice__remove {
  display: inline-block!important;
}


&.select2-container--focus {
      color: $color-secondary;
    box-shadow: 0 0 5px rgba($color-secondary, 0.1);
    border: 1px solid rgba($color-secondary, 1);


  .select2-selection--multiple {
    border: 0;
    outline: 0;
  }
}

&.select2-container--disabled {
  .select2-selection--multiple {
    background-color: #eee;
    cursor: default;
  }

  .select2-selection__choice__remove {
    display: none;
  }
}

.selection {
  width: 100%;
}


