//---------------------------------------------//
// Forms - Outline
// Basic styles for form elements
// Docs - http://outlinecss.co.uk/get-started.html#forms
//---------------------------------------------//


main form {
  background: $color-transparency;
  padding: 0.125rem 1rem 0.5rem 1rem;
  margin-bottom: 1em;
  max-width: 1200px;
}

main form.narrow {
  max-width: 37.5rem;
}



.delete-form {
  padding: 0;
  background: transparent;
}

.input-group-addon {
  position: relative;
  top: 1px;
  &:first-child {
    padding-left: .375rem
  }
  * {
    vertical-align: text-top
  }
}

form,
fieldset {
  margin-bottom: $spacing;
}

label {
  margin-bottom: .25rem;
  display: block;
  cursor: pointer;
  word-wrap: break-word;
  padding-right: 1.5rem;
  flex-shrink: 0;
}

fieldset {
  margin-top: $spacing;
  margin-bottom: $spacing;
  padding: $spacing;
}

select.input-transparent {
  outline: 0;
  appearance: none;
}

#idpc_dropdown {
  width: 200px;
}
legend {
  margin: 0 -1rem 0.5rem;
  color: $color-secondary;
  font-family: $font-bold;
  font-size: 1rem;
  padding: 0.5rem 1rem;
  display: block;
  width: calc(100% + 2rem);
}

.form-group,
%form-group {
  @extend %clearfix;
  break-inside: avoid;
}

.form-group {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  align-items: top;
}

.form-group--select {
  flex-wrap: wrap;
  label {
    flex-shrink: 0;
  }
}

.form-group label {
  width: 230px;
  padding: .375rem .375rem .375rem 0;
}

.form-wide-label {
  .form-group label {
    width: 37.5rem;
  }
  .select2-container, .form-group > div {
    padding-left: 2rem;
    width: calc(100% - 37.5rem)
  }
}

.form-control,
select.input-transparent,
%input,
 {
  flex: 1;
  padding: .375rem;
  display: block;
  height: 2.375rem;
  font: inherit;
  line-height: 1.5;
  color: $color-secondary;
  background: transparent;
  border: 0;
  border-bottom: 1px solid transparent;
  outline: 0;
  border-radius: 0;
  transition: all 0.30s ease-in-out;
  position: relative;
  &:focus {
    color: $color-secondary;
    // box-shadow: 0 0 5px rgba($color-secondary, 0.1);
    border-bottom: 1px solid rgba($color-secondary, 1);
  }
}

.input-group-btn {
    // background-color: $color-transparency;
    display: flex;
    .btn {
      margin: 0;
      color: $color-secondary;
    }
    &:last-child .btn {
      padding-right: 0;
    }
}


.select2-container,
.form-group > div {
  flex-grow: 1;
  display: flex;
  align-items: baseline;
  color: $color-secondary;
  width: calc(100% - 230px);
}

.form-group .input-group {
  align-items: center;
}


.help-block {
  margin: 0;
  padding: .375rem 0;
  width: 100%;
  // padding-left: 200px;
  // height: 2.375rem;
  font-size: 11px;
  line-height: 1.5;
}

