#alert-wrapper {
  position: fixed;
  bottom: 1em;
  right: 1em;
  width: 50%;
  max-width: 20em;
  background: $color-transparency;
}

.alert {
	padding: 1em;
	position: relative;
}

.close {
	position: absolute;
	top: 0;
	right: 0;
	padding: 0.5em;
}

.alert-success {
	border-top: 2px solid #2f422f
}

.alert-warning {
	border-top: 2px solid #574b37
}