//---------------------------------------------//
// Pagination - Outline
// Basic styling for pagination
// Docs - http://outlinecss.co.uk/get-started.html#pagination
//---------------------------------------------//

.pagination {
  border-top: .0625rem solid $color-border;
  border-bottom: .0625rem solid $color-border;

  margin-bottom: $spacing;
  // font-family: $font-secondary;
  text-align: left;
  margin-left: 0;
  list-style: none;
  padding: 0.375rem;

  li {
    margin: 0 .5rem 0 0;
    display: inline-block;

    &:last-child {
      margin-right: 0;
    }

    &:hover {
      background-color: $color-transparency;
      border-radius: 3px;
    }

    a {
      display: inline-block;
      padding: .25rem .75rem;
    }

    &.pager--current {
      background-color: $color-primary;

      a {
        color: #fff;
      }
    }
  }
}