/**
 * Navbar
 */
.navbar__list {
	float:right;
	text-align: right;
	@extend %space-top;
}

.navbar__logo {
	display: block;
	margin-bottom: $spacing / 2;
}

.navbar__link {
	display: block;
}

.navbar__item {
	position: relative;
	cursor: pointer;
}
.navbar__dropdown {
	display: none;
	position: absolute;
	right: 0.5em;
}

.navbar__item:hover .navbar__dropdown{
	display: block
}