//---------------------------------------------//
// Buttons - Outline
// CSS styles for buttons
// Docs - http://outlinecss.co.uk/get-started.html#buttons
//---------------------------------------------//

button,
.btn,
%btn {
  font-family: 'ProximaNova-Regular';
  appearance:none;
  margin-bottom: 1rem;
  padding: .5rem 1rem;
  display: inline-block;
  font-size: inherit;
  font-weight: normal;
  text-align: center;
  line-height: 1;
  background-color: transparent;
  color: $color-secondary;
  border: none;
  outline: none;
  cursor: pointer;
  vertical-align: middle;
  transition: all .3s ease-in-out;

  &:hover,
  &:focus,
  &:active
  &--active {
    color: $color-secondary;
    text-decoration: none;
  }

  &.btn--disabled, &[disabled] {
    cursor: not-allowed;
    opacity: .6;
  }
}
form> button {
  padding-left: 0;
}

.btn-create {
  padding-left: 0;
}

.transparent-wrapper .btn-create {
  padding-left: 1rem;
}