.client-nav {
    border-bottom: .0625rem solid $color-border;
    border-top: .0625rem solid $color-border;
    padding: 0.5rem 0;
    margin-bottom: 0.5rem;
    position: relative;
    min-height: 3.625rem;
}

.client-nav--open .client-nav {
    border: 0;
    padding: 0;
    margin-bottom: 0.5rem;
    .client-nav__list {
    width: 100%;

    }
	// background: $color-transparency;

}

.client-nav__list {
	background: $color-transparency;
    position: absolute;
    z-index: 1;
}
.client-nav__item a {
	display: block;
	padding: 0.5rem;
}

.client-nav__item:nth-child(even) a{
	background: $color-transparency;
}

.client-nav__list li:not(:first-child) {
	max-height: 0;
	overflow: hidden;
	transition: all 0.2s;
    a {
        border-top: .0625rem solid $color-border;
    }
}
.client-nav__list:hover li:not(:first-child) {
	max-height: 100px;
}
.client-nav--open .client-nav__list li:not(:first-child),
.client-nav__list:hover li:not(:first-child) {
	max-height: 100px;
}

.client-nav__list:hover li:not(:first-child) {
	background: black;
}

.client-nav--open .client-nav__list li:not(:first-child) {
	background: transparent;
}
