.nestable {
	margin-bottom: $spacing / 4;
	li {
		background: $color-transparency;
	    border-top: .0625rem solid $color-border;
	    position: relative;
	    display: flex;
	    flex-wrap: wrap;

	    div, .show-more-docs {
	    	padding: 0.5rem;
	    }
	    div i {
	    	margin-right: 0.5rem;
	    }
	    .doc, .show-more-docs {
	    	justify-content: space-between;
	    	padding-left: 2.5rem;
	    	align-items: center;
	    }
	    .doc-links {
	    	font-size: 1.2rem;
	    	line-height: 1;
	    	i {
	    		margin-right: 0;
	    	}
	    }
	    a {
	    	text-decoration: none;
	    	outline: 0;
	    }
	}
	ol {
		width: 100%;
	}
	.nestable-button {
		margin: 0;
		position: absolute;
		right: 0rem;
		top: 0rem;
	}
}

#docs-search {
	background: $color-transparency;
	width: 100%;
}

