//---------------------------------------------//
// Tables - Outline
// Basic styles for tables
// Docs - http://outlinecss.co.uk/get-started.html#tables
//---------------------------------------------//

table {
  max-width: 100%;
  width: 100%;
  margin-bottom: $spacing;
  border-spacing: 0;
  border-collapse: collapse;
  border: 0;

  th,
  tfoot td {
    text-align: left;
  }

  th,
  td {
    padding: .625rem .625rem .625rem 1rem;
    vertical-align: top;
  }
}

.seperator:last-child {
  display: none;
}
table,
.table {
  thead > tr {
    border-bottom: .0625rem solid $color-border;
    font-family: $font-bold;
  }
  tbody > tr {
    border-bottom: .0625rem solid $color-border;
  }
}

.table-striped tbody > tr:nth-child(odd) > td,
tr.odd td {
    background-color: $color-transparency;
  }


