main {
	padding-bottom: 6rem;
}

main .flex {
	display: flex;
	// > div:not(.content) {
	// 	margin-right: 2rem;
	// 	flex-basis: 33.33%;
	// }
	// flex-wrap: wrap;

}
/* Tells Susy there's 8 columns in the content */
.content {
  flex: 1;
}

.docs-wrapper,
.docs-viewer {
  width: 50%;
}

.docs-viewer {
	padding-left: 2rem;
	position: relative;
}

#loader {
	display: none;
	margin: auto;
	position: absolute;
	left: 50%;
	margin-left: -60px;
	z-index: -1;
	top: 0;
}

.pdfobject-container + #loader{
	display: block;
}

/* Tells Susy there's 4 columns in the sidebar, and its the last item on the row. */
div.sidebar {
  width: 37.5rem;
  flex-grow: 0;
  margin-right: 2rem;
}

div.sidebar--narrow {
	width: 20rem;
}
.transparent-wrapper {
	background: $color-transparency;
	margin-bottom: 1rem;
	> h3:not(.notes-title) {
		padding: .625rem .625rem .625rem 1rem;
		font-size: 1rem;
	    // border-bottom: .0625rem solid $color-border;
	    font-family: $font-bold;
	    color: $color-secondary;
	    margin: 0;
	}
	.notes-wrapper {
		padding: 0 1rem 0.1rem;
	}
}

.tab-pane .notes-wrapper {
		padding-left: 1rem;
	}
.wrapper{
	margin-bottom: 1rem;
	border-bottom: .0625rem solid $color-border;
}