//---------------------------------------------//
// Variables - Outline
// all variables...
// Docs - http://outlinecss.co.uk/get-started.html#variables
//---------------------------------------------//

// html
$html-font-base: 100%; // 16px
$font-base: .9375rem; // 16px
$spacing: 1.5rem; // 24px

// Fonts
$font-primary: 'ProximaNova-Light', serif; // body font
$font-bold: 'ProximaNova-Semibold', serif; // body font

// Colours
$color-primary: #9d9d9c;
$color-secondary: #d2d2d2;
$color-transparency: rgba(255,255,255,0.07);
$color-border: rgba(#c5c5c5, 0.3);

// Placeholder text colour
$placeholder-text: $color-secondary;

// Button text color
$btn-text-color: $color-primary;

// Breakpoints
$mobile-landscape: 30rem; // 480px
$tablet: 40rem; // 640px
$tablet-wide: 48rem; // 768px
$desktop: 64rem; // 1024px
$widescreen: 71.25rem; // 1140px



