$person-width: 200px;

$person-height: 100px;

#family-tree {
	width: 100%;
	height: 100vh;
	overflow: scroll;
}

.family-tree ul {
	display: none;
}

.family-tree > ul {
	display: block;
}

.family-tree {
		text-align: center;
	}
	.family-tree > ul {
		width: 100%;
	}

	.family-tree ul li{
		float:left;
	}

	.client:first-child{
		float: left;
		width: 50%;
		text-align: right;
	}

	.client:last-child{
		float: right;
		width: 50%;
		text-align: left;
	}

