.al_list {
	@extend %clearfix;
	display: flex;
}

.al_list__item {
  background: $color-transparency;
  float: left;
  width: 400px;
  border: 1px solid $color-border;
  margin-right: 1em;
}

.al_list__item h3 {
	font-size: 1.5em;
	margin: 0;
}

.al_list__item > div {
	padding: 0.25em 1em ;
	border-bottom: 1px solid $color-border;
	textarea {
		width: 100%
	}
}

.al_list__actions {
	@extend %clearfix;
	display: flex;
}
.al_list__actions > div{
	float:left;
	width: 50%;
	text-align: center;
	padding: 0.25em;
	&:first-child{
		border-right: 1px solid $color-border;
	}
}