.single-widget-container .widget{
	max-width: 30em;
	margin: 0 auto;
	padding-top: 20vh;
	header {
		border-bottom: .0625rem solid $color-border;
	}
	form {
		padding-top: 0.625rem;
		background: transparent;
		padding-left: 0;
		padding-right: 0;
		columns: 1;
	}
	input {
	  background: $color-transparency;
	}
	.form-group {
		margin-bottom: 5px;
	}
}